<template>
  <div>
      <div class="title">
        工单 
      </div>
      <div class="WOBox">
        <!-- 查询表单 -->
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="名称">
            <el-input v-model="formInline.user" placeholder="请输入名称"></el-input>
          </el-form-item>
          <el-form-item label="日期范围">
            <el-date-picker
              v-model="formInline.region"
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              value-format="yyyy-MM-dd">
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <!-- 新增 -->
        <div class="BtnrightBox">
          <el-button class="addCSS" type="warning" @click="addDialogFormVisible = true">新增</el-button>
          <el-button class="Btnright" type="success" @click="Fnviewmyworksheets">我的工单</el-button>
          <el-button class="Btnright" type="primary" @click="Fnviewworksheets()">全部工单</el-button>
        </div>
        <el-dialog title="新增" :visible.sync="addDialogFormVisible">
          <el-form ref="addForm" :model="addForm" :rules="addrules" label-width="80px">
            <el-form-item label="工单名称" prop="w_name">
              <el-input v-model="addForm.w_name"></el-input>
            </el-form-item>
            <el-form-item label="工单类型" prop="w_type">
              <el-input v-model="addForm.w_type"></el-input>
            </el-form-item>
            <el-form-item label="工单状态" prop="w_status">
              <el-select v-model="addForm.w_status" placeholder="请选择工单状态">
                <el-option label="创建" value="创建"></el-option>
                <el-option label="客服联系" value="客服联系"></el-option>
                <el-option label="指派工程师" value="指派工程师"></el-option>
                <el-option label="完成" value="完成"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="工单内容" prop="w_content">
              <el-input type="textarea" v-model="addForm.w_content"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="addOnSubmit">创建</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
        <!-- 记录打开页面 -->
        <el-dialog title="记录" :visible.sync="recordDialogFormVisible">
          <el-form ref="recordForm" :model="recordForm" label-width="80px">
            <el-form-item label="活动名称">
              <el-input v-model="recordForm.name"></el-input>
            </el-form-item>
            <el-form-item label="活动区域">
              <el-select v-model="recordForm.region" placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="活动时间">
              <el-date-picker
                v-model="recordForm.date"
                type="date"
                placeholder="选择日期"
                format="yyyy 年 MM 月 dd 日"
                value-format="yyyy/MM/dd">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="活动形式">
              <el-input type="textarea" v-model="recordForm.desc"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="recordOnSubmit">创建</el-button>
              <el-button>取消</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
        <!-- 表格 -->
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="w_id"
            label="工单编号"
            width="180">
          </el-table-column>
          <el-table-column
            prop="w_name"
            label="名称"
            width="180">
          </el-table-column>
          <!-- <el-table-column
            prop="w_type"
            label="类型"
            width="180">
          </el-table-column> -->
          <el-table-column
            label="时间">
            <template slot-scope="scope">
              {{scope.row.w_time | ItoTime}}
            </template>
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <!-- <el-button @click="handleClick(scope.row)"  type="warning" plain>明细</el-button>
              <el-button type="danger" @click.native="recordDialogFormVisible = true" plain>记录</el-button> -->
              <el-button type="primary" @click.native="Fneditworksheet(scope.row.w_id)" plain>查看</el-button>
              <el-button type="warning" @click.native="Fnmodifyeditworksheet(scope.row)" plain>修改</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 查看工单详情内容 -->
        <el-dialog title="工单详情" :visible.sync="ISeditworksheet">
          <el-row :gutter="20">
            <el-col :span="8">工单ID:</el-col>
            <el-col :span="16">{{Deditworksheet.w_id}}</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">工单名称:</el-col>
            <el-col :span="16">{{Deditworksheet.w_name}}</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">工单类型:</el-col>
            <el-col :span="16">{{Deditworksheet.w_type}}</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">工单状态:</el-col>
            <el-col :span="16">{{Deditworksheet.w_status}}</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">工单内容:</el-col>
            <el-col :span="16">{{Deditworksheet.w_content}}</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="8">时间:</el-col>
            <el-col :span="16">{{Deditworksheet.w_time | ItoTime}}</el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="16">&nbsp;</el-col>
            <el-col :span="8">操作人：{{Deditworksheet.w_e_id}}</el-col>
          </el-row>
        </el-dialog>
        <!-- 修改工单详情 -->
        <el-dialog title="修改工单详情" :visible.sync="modifyDialogFormVisible">
          <el-form ref="modifyForm" :model="modifyForm" :rules="addrules" label-width="80px">
            <el-form-item label="工单名称" prop="w_name">
              <el-input v-model="modifyForm.w_name"></el-input>
            </el-form-item>
            <el-form-item label="工单类型" prop="w_type">
              <el-input v-model="modifyForm.w_type"></el-input>
            </el-form-item>
            <el-form-item label="工单状态" prop="w_status">
              <el-select v-model="modifyForm.w_status" placeholder="请选择工单状态">
                <el-option label="创建" value="创建"></el-option>
                <el-option label="客服联系" value="客服联系"></el-option>
                <el-option label="指派工程师" value="指派工程师"></el-option>
                <el-option label="完成" value="完成"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="工单内容" prop="w_content">
              <el-input type="textarea" v-model="modifyForm.w_content"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="modifyOnSubmit">修改</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
  </div>
</template>

<script>
import url from "../../common/api";
export default {
    data() {
      return {
        formInline: {//搜索的数据
          user: '',
          region: []
        },
        tableData: [],//列表的数据
        addDialogFormVisible: false,//新增对话框打开
        addForm: {//新增材料提交
          user_id: 11,
          w_name: '',
          w_type: '',
          w_content: '',
          w_status: ''
        },
        modifyForm: {//修改工单提交
          user_id: "",
          w_name: '',
          w_type: '',
          w_content: '',
          w_status: ''
        },
        modifyDialogFormVisible:false,//修改工单提交对话框打开
        addrules: {//新增材料提交验证
          w_name: [
            { required: true, message: '请输入工单名称', trigger: 'blur' }
          ],
          w_type: [
            { required: true, message: '请输入工单类型', trigger: 'blur' }
          ],
          w_content: [
            { required: true, message: '请输入工单内容', trigger: 'blur' }
          ],
          w_status: [
            { required: true, message: '请选择工单状态', trigger: 'blur' }
          ]
        },
        recordDialogFormVisible:false,//记录项目
        recordForm: {//记录材料提交
          number: '',
          name: '',
          region: '',
          date: new Date().toLocaleDateString(),
          desc: ''
        },
        ISeditworksheet:false,//查看工单详情内容对话框标识
        Deditworksheet:{}//查看工单详情内容
      }
    },
    methods: {
      onSubmit() {//查询按钮点击
        if(this.formInline.region){
          url.searchworksheets({
            w_name:this.formInline.user,
            start_time:this.formInline.region[0],
            end_time:this.formInline.region[1],
          })
          .then(res=>{
            console.log(res,'-----------查询工单')
            let xx=res.data.data
            let newlist=[]
            xx.forEach(element => {
            element.fields.w_id=element.pk
            newlist.push(element.fields)
            });
            this.tableData=newlist
          })
        }else{
          url.searchworksheets({
            w_name:this.formInline.user,
            start_time:"",
            end_time:"",
          })
          .then(res=>{
            console.log(res,'-----------查询工单')
            let xx=res.data.data
            let newlist=[]
            xx.forEach(element => {
            element.fields.w_id=element.pk
            newlist.push(element.fields)
            });
            this.tableData=newlist
          })
        }

      },
      handleClick(i){//
        this.$router.push("/bkIndex/WOsingle")
        console.log(i,'表格中每一行的明细')
      },
      addOnSubmit() {//打开新增中的提交按钮
        this.$refs['addForm'].validate((valid) => {
          if (valid) {
            console.log(this.addForm,'--------------111!');
            this.addDialogFormVisible = false
            url.addworksheet(this.addForm)
            .then((res) => {
              if(res.data.code[0]=='200'){
                this.$message.success(res.data.message[0])
                //创建工单成功---请求工单列表
                this.Fnviewworksheets()
              }
            }).catch((err) => {
              
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      modifyOnSubmit(){//打开修改中的提交按钮
        this.$refs['modifyForm'].validate((valid) => {
          if (valid) {
            this.modifyDialogFormVisible = false
            this.modifyForm.user_id=11
            console.log(this.modifyForm,'修改参数传递')
            url.Peditworksheet(this.modifyForm)
            .then((res) => {
              console.log(res,'--------修改工单内容')
              if(res.data.code[0]=='200'){
                this.$message.success(res.data.message[0])
                //创建工单成功---请求工单列表
                this.Fnviewworksheets()
              }
            }).catch((err) => {
              
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      recordOnSubmit() {//打开记录
        console.log(this.recordForm,'--------------12!');
        this.recordDialogFormVisible = false
      },
      Fnviewmyworksheets(){//查看我的工单列表,需后台工作人员身份
        url.viewmyworksheets({
          user_id:11
        }).then(res=>{
          console.log(res,'------工单')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.w_id=element.pk
          newlist.push(element.fields)
          });
          this.tableData=newlist
          console.log(newlist,'-----------查看我的工单列表,需后台工作人员身份')
        })
      },
      Fneditworksheet(i){//查看工单详情
        this.ISeditworksheet=true
        url.editworksheet({
          user_id:11,
          w_id:i
        }).then(res=>{
          console.log(res,'=========查看工单详情')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.w_id=element.pk
          newlist.push(element.fields)
          });
          this.Deditworksheet=newlist[0]
          console.log(newlist,'--------查看工单详情处理后的数据')
        })
      },
      Fnmodifyeditworksheet(i){
        this.modifyDialogFormVisible = true
        console.log(i,'--这一行的数据')
        this.modifyForm=i
      },
      Fnviewworksheets(){//查看全部工单
        url.viewworksheets({
          user_id:11
        }).then(res=>{
          console.log(res,'-----------我的工单列表')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.w_id=element.pk
          newlist.push(element.fields)
          });
          this.tableData=newlist
        })
      }
    },
    mounted(){
      //请求工单列表
      this.Fnviewworksheets()
    },
    filters:{
      ItoTime(aa){
        if(aa){
          let subaa=aa.substring(0,aa.indexOf('T'))
          return subaa
        }else{
          return ''
        }
      }
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.WOBox
    padding 30px
    .BtnrightBox
      position relative
      .addCSS
        margin-bottom 23px
      .Btnright
        float right 
>>>.el-row
    margin 20px 20px
    padding-bottom 10px
    font-size 16px
    border-bottom 1px solid #f1f1f1
>>>.el-col-8
  text-align right
</style>


